.o-container
   width: 95%
   max-width: $container-width
   display: block
   margin: 0 auto
   position: relative
   &::before,
   &::after
      display: table
      content: ""
      clear: both

.o-grid-row
   display: flex
   flex-wrap: wrap
   width: 100%
   &.row-end
      @include flexEnd
   &.row-center
      @include flexCenter
   .col
      width: auto
      padding: 0 10px
      &.size-12
         width: 100%
      &.size-11
         width: 91.66666667%
      &.size-10
         width: 83.33333333%
      &.size-9
         width: 75%
      &.size-8
         width: 66.66666667%
      &.size-7
         width: 58.33333333%
      &.size-6
         width: 50%
      &.size-5
         width: 41.66666667%
      &.size-4
         width: 33.33333333%
      &.size-3
         width: 25%
      &.size-2
         width: 16.66666667%
      &.size-1
         width: 8.33333333%
         
